import { color } from "storybook/internal/theming";
import { createCard } from "./Card";

export default {
  title: "Component/Card",
  component: createCard,
  argTypes: {
    color: { control: "color" }, // Control for color
    fontSize: { control: "text" }, // Control for font size
  },
};

const Template = (args) => {
  const card = createCard(args); // Pass args to createCard function
  const container = document.createElement("div");
  container.appendChild(card); // Append card to a container
  return container; // Return container to Storybook
};

export const Default = Template.bind({});
Default.args = {
  color: "black",
  fontSize: "16px",
};
